@import '../../../scss/variables';
.testimonial1-container{
    width: 358px;
    height: 396px;

    .text1-container{
        position: relative;
        width: 100%;
        height: 266px;
        background-color: white;
        padding: 22.2px 43px 66px 43px;
        box-shadow: 0px 10px 20px rgba(41, 126, 255, 0.15);
        border-radius: 10px;

        img{
            display: block;
            margin: 0 auto 19.2px;

        }

        p{
            font-size: 16px;
            line-height: 26px;
        }
        .profile-pic{
            position: absolute;
            width: 108px;
            height: 108px;
            border-radius: 50%;
            margin-bottom: 0;
            top: 219px;
            left: 125px;
            object-fit: cover;
        }
        .blue-shadow{
            width: 108px;
            height: 108px;
            background-color: $blue;
            opacity: 0.15;
            border-radius: 50%;
            top: 219px;
            left: 125px;
        }
    }

    .name{
        margin-top: 80px;
        font-size: 16px;
        line-height: 26px;
        font-weight: bold;
    }

    .position{
        font-size: 14px;
        line-height: 26px;
        font-weight: normal;
    }

}
@media only screen and (min-width: 320px) and (max-width: 520px){
    .testimonial1-container{

        height: 500px;
        margin-bottom: 30px;
        margin-top: 30px;
        margin-left: auto;
        margin-right: auto;
        width: 90%;
    }
}

