@import "../../../scss/variables";
.card {

    .cont-btn-ver{
        display: flex;
        justify-content: center;
        align-items: center;

        .btn-ver {
            padding-left: 7px;
            padding-right: 7px;
            background-color: blue;
            margin-top: 10px;
            margin-bottom: 10px;
            border-radius: 5px;
            color: white;
            width: 100px;
            text-align: center;
            font-size: 12px;

          }
    }

  box-shadow: 0px 0px 10px #00000091;
  font-family: "Muli", sans-serif;
  width: 260px;
  height: 450px;
  background-color: #f6f6f6;
  border-radius: 10px;
  overflow: auto;
  position: relative;
  margin-top: 6px;
  margin-bottom: 30px;

  .card-image {
    width: 100%;
    height: 252px;
    object-fit: cover;
  }
  .blue-shadow {
    width: 100%;
    height: 252px;
    background-color: $blue;
    opacity: 0.15;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: unset;
  }
  .card-body {
    padding: 5px 10px 0 18px;
  }

  .card-content {
    border-bottom: 1px solid #c4c4c4;
    padding-bottom: 23px;
    a {
      text-decoration: none;
      color: $gray1;
      font-size: 12px;
      line-height: 30px;
      font-weight: bold;

      span {
        margin-left: 5px;
      }
      img {
        display: inline;
      }
    }
    h4 {
      margin: 0;
      margin-top: 5px;
      .card-title {
        color: black;
        font-size: 17px;
        line-height: 30px;
        font-weight: bold;
      }
    }

    p {
      overflow-y: auto;
      text-align: left;
      margin: 0;
      margin-top: 5px;
      color: $gray1;
      font-size: 14px;
      line-height: 30px;
      font-weight: bold;
      span {
        color: #c4c4c4;
        font-weight: 600;
      }
    }
  }

  .card-footer {
    display: flex;
    justify-content: space-between;
    margin-top: 8px;
    margin-bottom: 13px;
    .footer-item {
      p {
        display: inline;
        margin-left: 5px;
        font-size: 12px;
        line-height: 30px;
        color: $gray1;
      }
      img {
        display: inline;
      }
    }
  }
}

@media only screen and (min-width: 320px) and (max-width: 480px) {
  .card {
    width: 100%;
    height: 550px;
    padding: 0 20px;
    margin-bottom: 25px;
  }
}
